import {
  Button,
  Card,
  CardBody,
  Input,
  Pagination,
  Table,
  TableCell,
  TableContainer,
  TableFooter,
  TableHeader,
} from "@windmill/react-ui";
import { useContext } from "react";
import { useTranslation } from "react-i18next";

//internal import
import useAsync from "hooks/useAsync";
import useFilter from "hooks/useFilter";
import OrderServices from "services/OrderServices";
import ordersfromlocal from "../../utils/vendors";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import OrderTable from "components/vendors/vendorsTable";
import TableLoading from "components/preloader/TableLoading";
import { FiPlus } from "react-icons/fi";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useState } from "react";
import { getVendorLists } from "services/Api";

const Orders = () => {
  const {
    time,
    currentPage,
    searchText,
    status,
    // handleChangePage,
    handleSubmitForAll,
    resultsPerPage,
    startDate,
    endDate,
    lang,
  } = useContext(SidebarContext);

  const { t } = useTranslation();
  const [vendorData, setVendorData] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);
  const [wholeCount, setwholeCount] = useState({});
  const { data, loading } = useAsync(() =>
    OrderServices.getAllOrders({
      customerName: searchText,
      status,
      page: currentPage,
      limit: resultsPerPage,
      day: time,
      startDate,
      endDate,
    })
  );

  const { serviceData, globalSetting } = useFilter(data?.orders);

  const getVendors = async (pageNumber, search = "") => {
    const res = await getVendorLists(pageNumber, search);
    if (res.status === 200) {
      console.log(res, "resresres");
      setVendorData(res?.data?.data);
      setwholeCount(res?.data?.data?.pagination);
    }
  };

  const handleChangePage = (page) => {
    getVendors(page);
  };

  useEffect(() => {
    getVendors(pageNumber);
  }, []);

  // console.log("data in orders page", data);

  const handleSearch = (search) => {
    getVendors(pageNumber, search);
  };

  return (
    <>
      <PageTitle>{t("Vendors")}</PageTitle>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to="add-vendors">
                  {" "}
                  <Button className="w-full rounded-md h-12">
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Vendor")}
                  </Button>{" "}
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>
      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 dark:bg-gray-900">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{t("S.No")}</TableCell>
                <TableCell>{t("property Name")}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("GST")}</TableCell>
                <TableCell>{t("Category")}</TableCell>
                <TableCell>{t("Type Of service")}</TableCell>
                <TableCell>{t("Address")}</TableCell>
                <TableCell>{t("Email")}</TableCell>
                <TableCell>{t("Phone")}</TableCell>
                <TableCell>{t("Person to contact")}</TableCell>
                <TableCell>{t("Bank Details")}</TableCell>
                <TableCell>{t("Cheque Scan Copy")}</TableCell>
                <TableCell>{t("GST certificate")}</TableCell>
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>

            <OrderTable
              lang={lang}
              orders={vendorData?.vendors}
              globalSetting={globalSetting}
              getVendors={getVendors}
              pageNumber={pageNumber}
              currency={globalSetting?.default_currency || "$"}
              wholeCount={wholeCount}
            />
          </Table>

          <TableFooter>
            <Pagination
              totalResults={vendorData?.pagination?.total}
              resultsPerPage={vendorData?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Table navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no orders right now." />
      )}
    </>
  );
};

export default Orders;
