import { TableBody, TableCell, TableRow } from "@windmill/react-ui";
import PrintReceipt from "components/form/PrintReceipt";
import SelectStatus from "components/form/SelectStatus";
import Status from "components/table/Status";
import Tooltip from "components/tooltip/Tooltip";
import { useTranslation } from "react-i18next";
import { FiEdit, FiEye, FiTrash2, FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showDateTimeFormat } from "utils/dateFormate";
import image1 from "../../assets/img/day1.png";
import image2 from "../../assets/img/day2.png";
import useToggleDrawer from "hooks/useToggleDrawer";
import DeleteModal from "./DeleteVendors";
const OrderTable = ({ orders,getVendors,wholeCount,pageNumber, }) => {
  const { t } = useTranslation();
  const { title, serviceId, handleModalOpen, handleUpdate } = useToggleDrawer();
  console.log(orders, "orders");

  return (
    <>
      <DeleteModal id={serviceId} pageNumber={pageNumber} title={title} getVendors={getVendors} />
      <TableBody className="dark:bg-gray-900">
        {orders?.map((order, i) => (
          <TableRow key={i + 1}>
            <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">{order?.Property?.name}</span>
            </TableCell>

            <TableCell className="text-xs">
              <span className="text-sm">{order?.company_name}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">{order?.gst_number}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">{order?.service_category}</span>
            </TableCell>

            <TableCell className="text-sm">
              {order?.service_description}
            </TableCell>

            <TableCell className="text-sm textcorrect">{order?.address}</TableCell>
            <TableCell className="text-sm">{order?.email}</TableCell>

            <TableCell className="text-sm">{order?.mobile}</TableCell>
            <TableCell className="text-sm">
              {order?.person_to_contact_name}
              <br />
              {order?.person_to_contact_mobile}
              <br />
              {order?.pocEmail}
              <br />
            </TableCell>
            <TableCell className="text-sm">
              {order?.bank_branch_name}
              <br />
              {order?.bank_ifsc_code}
              <br />
              {order?.bank_account_number}
              <br />
            </TableCell>
            <TableCell className="text-left">
              {order?.cheque_file_path && (
                <img
                  src={order?.cheque_file_path}
                  className="mx-2"
                  alt="lang"
                />
              )}
            </TableCell>
            <TableCell className="text-left">
              {order?.gst_file_path && (
                <img src={order?.gst_file_path} className="mx-2" alt="lang" />
              )}
            </TableCell>

            <TableCell className="text-right flex justify-end">
              <div className="flex justify-between items-center">
                {/* <PrintReceipt orderId={order._id} /> */}

                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/Vendor-invoicelist/${order?.id}`}>
                    <Tooltip
                      id="view"
                      Icon={FiEye}
                      title={t("Invoice View")}
                      bgColor="#059669"
                    />
                  </Link>
                </span>
                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/edit-vendors/${order?.id}`}>
                    <Tooltip
                      id="edit"
                      Icon={FiEdit}
                      title={t("Edit Vendors ")}
                      bgColor="#059669"
                    />
                  </Link>
                </span>
                <button
                  onClick={() =>
                    handleModalOpen(order?.id, "vendor", "product")
                  }
                  className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
                >
                  <Tooltip
                    id="delete"
                    Icon={FiTrash2}
                    title={t("Delete")}
                    bgColor="#EF4444"
                  />
                </button>
              </div>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default OrderTable;
